import { Injectable } from '@angular/core';
import { pull } from 'lodash';
import { RuntimeViewModel } from './runtime-view.model';

@Injectable({
  providedIn: 'root'
})
export class RxRuntimeViewRegistryService {
  private activeRuntimeViews: RuntimeViewModel[] = [];

  register(runtimeViewModel: RuntimeViewModel): void {
    if (!this.activeRuntimeViews.includes(runtimeViewModel)) {
      this.activeRuntimeViews.push(runtimeViewModel);
    }
  }

  getAll(): RuntimeViewModel[] {
    return this.activeRuntimeViews;
  }

  unregister(runtimeViewModel: RuntimeViewModel): void {
    pull(this.activeRuntimeViews, runtimeViewModel);
  }
}
